.home-page-catalog-section {
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
}

.home-page-catalog-container {
    display: flex;
    flex-wrap: nowrap; /* Отключает перенос */
    flex-direction: column;
    align-items: center;
    width: 80%;
}

.home-page-catalog-container > .catalog {
    width: 100%;
    max-width: 1500px;
    min-width: 300px;
}

.home-page-catalog-container > .catalog > .product-list {
    display: flex;
    flex-wrap: nowrap; /* Отключает перенос */
    justify-content: space-evenly;
    gap: 20px;
    overflow-x: auto; /* Горизонтальная прокрутка */
    width: 100%;
}

.home-page-catalog-container > .catalog > .product-list > .product-item {
    flex: 1 1 auto; /* Адаптивное распределение места */
    max-width: 300px; /* Ограничиваем ширину */
    min-width: 150px; /* Минимальная ширина */
}

.home-page-catalog-container > .info-with-lines {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 20px;
    gap: 1%;
    margin-top: 2vh;
    margin-bottom: 30px;
}

.info-with-lines > div {
    height: 2px;
    background-color: #e37243;
    width: 50%;
}

.info-with-lines > .formal-title {
    font: 400 calc(14px + 2*(100vw / 1440)) Inter, sans-serif;
    text-align: center;
    width: 300px;
    color: #383534;
}

.link-to-catalog {
    margin-top: 30px;
    margin-bottom: 20px;
    display: flex;
    width: 100%;
    align-items: center;
    gap: 2vw;
    flex-direction: row;
    justify-content: flex-end;
}

.link-to-catalog > p {
    font: 400 calc(12px + 2*(100vw / 1440)) Inter, sans-serif;
    text-align: center;
    color: #383534;
}

.link-to-catalog > .call-to-catalog-link {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Общий стиль прокрутки */
.home-page-catalog-container > .catalog > .product-list {
    display: flex;
    flex-wrap: nowrap; /* Отключаем перенос */
    justify-content: space-evenly;
    gap: 20px;
    overflow-x: auto; /* Горизонтальная прокрутка */
    width: 100%;
    padding-bottom: 10px; /* Добавляем отступ снизу для полосы прокрутки */
}
.home-page-catalog-container > div > .title-section{
    align-self: center;
    padding: 0;
    margin: 0;
    font: 700 calc(20px + 6*(100vw / 1440)) Inter, sans-serif;
    color: #383534;
    padding: 10px 0;
}
.info-with-lines > .formal-title {
    font: 400 calc(14px + 2*(100vw / 1440)) Inter, sans-serif;
    text-align: center;
    width: 300px;
    color: #383534;
}

.link-to-catalog > p{
    font: 400 calc(12px + 2*(100vw / 1440)) Inter, sans-serif;
    text-align: center;
    color: #383534;
}
/* Стилизация полосы прокрутки */
.home-page-catalog-container > .catalog > .product-list::-webkit-scrollbar {
    height: 10px; /* Высота полосы прокрутки */
    background: #f0f0f0; /* Цвет фона полосы прокрутки */
    border-radius: 3px; /* Закругленные углы */
}

/* Стилизация бегунка (ползунка) */
.home-page-catalog-container > .catalog > .product-list::-webkit-scrollbar-thumb {
    background: #e37243; /* Цвет бегунка */
    border-radius: 3px; /* Закругленные углы бегунка */
    border: 1px solid #f0f0f0; /* Обводка для бегунка */
}

/* Стилизация при наведении */
.home-page-catalog-container > .catalog > .product-list::-webkit-scrollbar-thumb:hover {
    background: #d3603d; /* Цвет бегунка при наведении */
}

/* Стилизация полосы прокрутки для Firefox */
.home-page-catalog-container > .catalog > .product-list {
    scrollbar-width: thin; /* Тонкая полоса прокрутки */
    scrollbar-color: #e37243 #f0f0f0; /* Цвет бегунка и фона */
}
